.alerta{
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    &.error{
        color: $rojo;
    }
    &.exito{
        color: $verde;
    }
}

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $verde;
        th{
            color: $blanco;
            padding: 2rem;
        }
    }
    tbody{
        td{
            text-align: center;
        }
    }
    .imagen-tabla{
        width: 10rem;
        margin: 0 auto;
    }
}
.imagen-propiedad{
    width: 20rem;
}